import { Message } from "element-ui";
import axios from "axios"
// import '../../public/config.js'
import { getCookie } from "../utils/common.js";

let baseUrl = ''

let httpService = axios.create({
  baseUrl: baseUrl,
  timeout: 60000
})

httpService.interceptors.request.use(config => {
  let tokenString = getCookie("token")
  let token = tokenString ? decodeURIComponent(tokenString) : null
  // 
  if (token) {//cookietoken
    config.headers.token = token
    config.headers.Authorization = 'Bearer ' + token
  }
  return config;
}, err => {
  Promise.reject(err);// 
})


httpService.interceptors.response.use(response => {
  // TODO:code
  if (response.data.code === 200) {
    console.log('')
  }
  if (response.data.code === 300) {
    console.log(response.data.msg)
  }
  return response;
}, err => {
  // TODO:code
  if (err.response.code === 301 || err.response.code === 401) {
    console.log('');
    //fullPath ---(index)    
    // (index)   let path = this.$route.query.redirect || '/index.js';   this.$router.push(path);
    setTimeout(() => {
      this.$router.replace({
        path: '/Login',
        query: {
          redirect: this.$router.currentRoute.fullPath
        }
      });
    }, 1000);
    document.cookie = "token=;";//token
  }
  if (err.response.code === 500) {
    Message({
      message: "",
      type: 'error'
    })
  }
  return Promise.reject(err);
})


export function get (url, params = {}, headers = {}) {
  return new Promise((resolve, reject) => {

    httpService({
      url: url.indexOf('.json') > -1 ? url : baseUrl + url,
      method: 'get',
      params: params,
      headers: headers
    }).then(res => {
      resolve(res);
    }).catch(err => {
      reject(err);
      Message({
        message: "",
        type: 'error'
      })
    })
  })
}


export function post (url, params = {}, headers = {}) {
  return new Promise((resolve, reject) => {
    httpService({
      url: baseUrl + url,
      method: 'post',
      data: params,
      headers: headers
    }).then(res => {

      resolve(res);
    }).catch(err => {
      reject(err);
      Message({
        message: "",
        type: 'error'
      })
    })
  })
}

export default {
  get,
  post
}
