import Map from 'ol/Map.js';
import View from 'ol/View.js';
import {  ScaleLine,  MousePosition, FullScreen, OverviewMap, ZoomSlider, defaults as defaultControls } from 'ol/control';
import TileLayer from 'ol/layer/Tile.js';
import { toStringXY } from 'ol/coordinate.js';
import { DragRotateAndZoom, defaults as defaultInteractions} from 'ol/interaction.js';

import { initVectoryLayers } from "@/mapConfig/mapVectorLayers.js"
import mapConfig from "./mapUnderlayerConfig"

let underlayerConfig = {};
// keymap.setLayers()
mapConfig.underlayersArr.forEach(layer => {
    if (!(layer.values_.id in underlayerConfig)) {
        underlayerConfig[layer.values_.id] = []
    }
    underlayerConfig[layer.values_.id].push(layer)
})

// 
export const controlConfig = {
    'ScaleLine': {
        visible: true,
        type: 'line',
    },
    'MousePosition': {
        visible: true,
        projection: "EPSG:4326",
        length: 6  
    },
    'FullScreen': true,
    'ZoomSlider': true,
    'OverviewMap': true
}

const controlArr = function (controlConfig, layer){
    let controls = []
    if (!controlConfig){
        controls = [] 
    } else {
        if (controlConfig.ScaleLine.visible) {
            controls.push(initScaleLine(controlConfig.ScaleLine.type))
        } 
        if (controlConfig.MousePosition.visible) {
            controls.push(initMousePosition(controlConfig.MousePosition.projection, controlConfig.MousePosition.length))
        }
        if (controlConfig.FullScreen) {
            controls.push(initFullScreen())
        } 
        if (controlConfig.ZoomSlider) {
            controls.push(initZoomSlider())
        }
        if (controlConfig.OverviewMap && layer) {
            controls.push(initOverviewMap(layer.getSource()))
        }
    }
    return controls
}
/**
 * 
 * @params target 
 * @params viewConfig view
 * @params underlayerType  
 */
export const initMap = function (target, viewConfig, controlConfig, underlayerType) {
    let type = underlayerType ? underlayerType : 'TDT_DXT';
    let underlayer = underlayerConfig[type][0];
    underlayer.setVisible(true);
    let controls = controlArr(controlConfig, underlayer)
    let map = new Map({
        target: target,
        layers: mapConfig.underlayersArr,
        view: new View(viewConfig),
        controls: defaultControls().extend(controls),
        interactions: defaultInteractions({
            doubleClickZoom: false
        }).extend([new DragRotateAndZoom()]),
    });
    window.olMap = map;
    
    initVectoryLayers(map)
    return map
}

/**
 *   
 * @param type  key   "TDT_YXT"
 * */
export const changeUnderlayer = function (type) {
    if (!type) return
    if (type in underlayerConfig) {
        mapConfig.underlayersArr.map(item => {
            if (item.values_.id === type) {
                item.setVisible(true)
            } else {
                item.setVisible(false)
            }
        })
    }
}

/**
 * 
 * @params type   line  bar
*/
export const initScaleLine = function (type) {
    let scaleLineControl = null;
    if (type === "line") {
        scaleLineControl = new ScaleLine({
            units: 'metric',
            className: "ol-scale-line",
        });
    } else {
        scaleLineControl = new ScaleLine({
            units: 'metric',
            className: "ol-scale-bar",
            bar: true,
        });
    }
    return scaleLineControl
}

/**
 * 
 * @params projection  EPSG:4326 EPSG:3857 
 * @params length 
*/
export const initMousePosition = function (projection, length) {
    let mousePositionControl = new MousePosition({
        className: "ol-mouse-position",
        projection: projection,
        coordinateFormat: (coord) => {
            return toStringXY(coord, length);
        },
        // placeholder: ''
    })
    return mousePositionControl
}


/**
 * 
 * @param source  
*/
export const initFullScreen = function (source) {
    let fullScreenControl = new FullScreen({
        className: 'ol-full-screen',
        // label: '',
        // labelActive: '',
        activeClassName: 'ol-full-screen-true',
        inactiveClassName: 'ol-full-screen-false',
        tipLabel: '',
        source: source,
    })
    return fullScreenControl
}

/**
 * 
 * params source 
*/

export const initOverviewMap = function (source) {
    let overviewMapControl = new OverviewMap({
        className: 'ol-overviewmap',
        collapsed: true,
        // collapseLabel: '',
        collapsible: true,
        // label: '',
        layers: [
            new TileLayer({
              source: source,
            }),
        ],
        tipLabel: "",
    })
    return overviewMapControl
}

/**
 * 
*/
export const initZoomSlider = function () {
    let zoomSliderControl = new ZoomSlider({
        className: 'ol-zoomslider',
        duration: 100,
    })
    return zoomSliderControl
}